.section-first-title {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    color: var(--main-color);
    font-size: 32px;
}

.section-first-title::after {
    content: '';
    position: absolute;
    border: 2px solid var(--main-color);
    width: 200px;
    bottom: 0;
    left: 410px;
}

.section-first-title::before {
    content: '';
    position: absolute;
    border: 2px solid var(--main-color);
    width: 200px;
    bottom: 0;
    right: 410px;
}

.icon-mid {
    color: var(--main-color);
    font-size: 30px;
    position: absolute;
    bottom: -25px;
    left: 630px;
}

.section-first-title h3 {
    margin: 0px 10px;
}

.section_pay_money {
    margin: 0px auto 0px auto;
    width: 860px;
    height: auto;
}

.top_infomation {
    margin: 0 auto;
    width: 860px;
    height: 220px;
    display: inline-flex;
    justify-content: center;
}

.stage_2_carts {
    position: relative;
    z-index: 0;
    color: green;
    padding: 10px;
    margin-right: 10px;
    border-radius: 10px;
    background-color: aquamarine;
}

.stage_2_carts p {
    margin: 5px 0px;
    color: green;
}

.stage_2_carts_icon{
    position: absolute;
    bottom: 40px;
    right: -55px;
    display: flex;
    margin: 0px 20px;
}

// scss cho form thông tin
.infomation_client_pay {
    max-width: 500px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f2f2f2;
    border-radius: 5px;

    form {
        h3 {
            margin-bottom: 10px;
            font-size: 18px;
            font-weight: bold;
        }

        .form-group {
            margin-bottom: 20px;

            label {
                display: block;
                margin-bottom: 5px;
                font-weight: bold;
            }

            input[type='text'] {
                width: 100%;
                padding: 10px;
                border: 1px solid #ccc;
                border-radius: 3px;
            }
        }

        table.list_products {
            width: 100%;

            tr {
                td {
                    img {
                        width: 50px;
                        height: 50px;
                        object-fit: cover;
                        border-radius: 50%;
                    }
                }

                .quantity {
                    display: flex;
                    align-items: center;

                    button {
                        padding: 5px 10px;
                        background-color: #eee;
                        border: none;
                        border-radius: 3px;
                        cursor: pointer;
                    }

                    .quantity_value {
                        margin: 0 10px;
                        font-weight: bold;
                    }
                }

                .total_money {
                    font-weight: bold;
                }
            }
        }

        button[type='submit'] {
            padding: 10px 20px;
            background-color: #4caf50;
            color: #fff;
            border: none;
            border-radius: 3px;
            cursor: pointer;
            
            &:hover{
                background-color: #003802;
                color: #fff;
            }
        }
    }
}
